<template>
  <div id="wa_2">
    <div id="sample">
      <br>
      <div style="display:inline-block;height: 65%;width: 7%;line-height: 65px;margin-left: 12px;border-radius: 18%;text-align: center;font-size: 20px;background-color: white;">词性类别图示</div>
      <div v-for="s in sample" :key="s.word" style="display:inline-block;height: 65%;width: 7%;line-height: 65px;margin-left: 12px;border-radius: 18%;text-align: center;color: white;font-size: 20px;" :class="s.flag">{{s.word}}</div>
    </div>
    <div id="fir_msg" class="msg">
      <h3>修改前分析结果:</h3>
      <div v-for="i in arr" :key="i.key" class="basic" :class="i.flag">{{i.word}}</div>
    </div>
    <div id="sec_msg" class="msg">
      <h3>修改后分析结果:</h3>
      <div v-for="e in arr2" :key="e.key" class="basic" :class="e.flag">{{e.word}}</div>
    </div>
    <div id="ribbon">
      <div id="diy">
        <p>用户自定义词语</p>
        <hr>
        词语：
        <input type="text" v-model="form_data.word">
        词性:
        <select v-model="form_data.flag">
          <option value="">请选择词性</option>
          <option value="n">名词</option>
          <option value="a">形容词</option>
          <option value="v">动词</option>
          <option value="c">副词</option>
          <option value="nz">专有名词</option>
          <option value="r">代词</option>
          <option value="m">数词</option>
          <option value="p">量词</option>
          <option value="c">连词</option>
          <option value="u">助词</option>
          <option value="x">标点符号</option>
        </select>
        <button @click="add_diy">确认添加</button>&nbsp;
        <button @click="refresh">重新进行词性分析</button>&nbsp;
        <button @click="clear_diylist">清除所有自定义词语</button>
      </div>
      <button style="height: 30px;font-size: 18px;display: block;">存储到数据中心</button>
      <button @click="to_excel" style="height: 30px;font-size: 18px;display: block;">导出至文件</button> 
    </div>
    <div id="list">
      用户自定义词汇列表
      <hr>
      <div style="display:inline-block;height: 3.5%;width: 30%;border: 1px black solid;">词语</div>
      <div style="display:inline-block;height: 3.5%;width: 30%;border: 1px black solid;">词性</div>
      <div v-for="item in diy_list" :key="item.key">
        <div style="display:inline-block;height: 10%;width: 30%;border: 1px black solid;">{{item.word}}</div>
        <div style="display:inline-block;height: 10%;width: 30%;border: 1px black solid;">{{item.flag}}</div>
        <div style="display:inline-block;height: 10%;width: 30%;border: 1px black solid;cursor: pointer;" @click="delete_item(item)">删除</div>
      </div>
    </div>
    <!-- <a :href="xls_save" ref="save"></a> -->
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data(){
    return{
      arr:[
        /*{word:'全世界', flag:'n', key:1},
        {word:'无产者', flag:'n', key:2},
        {word:'联合', flag:'v', key:3},
        {word:'起来', flag:'adv', key:4},*/
      ],
      arr2:[
        /*{word:'全世界', flag:'n', key:1},
        {word:'无产者', flag:'n', key:2},
        {word:'联合', flag:'v', key:3},
        {word:'起来', flag:'adv', key:4},
        {word:'全世界', flag:'n', key:5},
        {word:'无产者', flag:'n', key:6},
        {word:'联合', flag:'v', key:7},
        {word:'起来', flag:'adv', key:8},*/
      ],
      str_2:this.str_1,
      form_data:{
        word:'',
        flag:''
      },
      diy_list:[],
      xls_save:'',
      sample:[
        {word:'名词', flag:'n'},
        {word:'动词', flag:'v'},
        {word:'形容词', flag:'a'},
        {word:'副词', flag:'d'},
        {word:'专有名词', flag:'nz'},
        {word:'代词', flag:'r'},
        {word:'数词', flag:'m'},
        {word:'量词', flag:'q'},
        {word:'连词', flag:'c'},
        {word:'助词', flag:'u'},
        {word:'标点符号', flag:'x'},
      ],
      p:0,
    }
  },
  mounted(){
    // console.log(this.str_2);
    this.refresh();
  },
  methods:{
    refresh(){
      axios({
      url: 'http://127.0.0.1:8080/to_dir',
      data: JSON.stringify({
        text:this.str_2,
        diy:this.diy_list,
      }),
      method: 'post',
      // headers: { 'Content-Type': 'multipart/form-data'},
    }).then(
      response => {
        this.arr = this.arr2;
        console.log(response.data);
        this.arr2 = [];
        for(var i=0;i<response.data.word.length;i++){
          this.arr2.push({
            word: response.data.word[i],
            flag: response.data.flag[i],
            key: i
          })
        }
        console.log(this.arr2);
        !this.p&&(this.arr = this.arr2);
      },
      error => {
        console.log(error.message);
      }
    )
    },
    to_excel(){
      var a  = JSON.stringify({
          list:this.arr,
        });
      axios({
        url: 'http://127.0.0.1:8080/wa_save',
        data: JSON.stringify({
          list:this.arr,
        }),
        dataType:'json',
        method: 'post',
        // headers: { 'Content-Type': 'multipart/form-data'},
      }).then(
        response => {
            console.log(response.data);
            var a = document.createElement('a');
            var e = document.createEvent('MouseEvents');
            a.download = "分析结果";
            a.href = '/group1/wordanaly/'+response.data;
            e.initEvent('click', true, true);
            a.dispatchEvent(e);
            window.URL.revokeObjectURL(a.href);
        },
        error => {
          console.log(error.message);
        }
      )
    },
    add_diy(){
      if(this.form_data.word =='' || this.form_data.flag == ''){
        return alert('请添加有效的词语及词性')
      }
      this.diy_list.push({
        word:this.form_data.word,
        flag:this.form_data.flag,
        key:this.diy_list.length+1,
      })
    },
    delete_item(item){
      this.diy_list.splice(item.key - 1, 1);
      for(var i = item.key-1;i<this.diy_list.length+1;i++){
        this.diy_list[i].key -= 1;
      }
    },
    clear_diylist(){
      this.diy_list = [];
    }
  },
  props:['str_1'],
}
</script>

<style>
  #wa_2{
    position: absolute;
    top: 100px;
    left: 250px;
    bottom: 0px;
    right: 0px;
    background-color:#f4f4f4;
    padding-left: 75px;
  }
  #sample{
    height: 100px;
    background-color: #f4f4f4;
    width: 100%;
    border-radius: 5%;
  }
  .msg{
    background-color: white;
    height: 450px;
    width: 1400px;
    margin-top: 50px;
    border-radius: 3%;
  }
  #ribbon{
    position: absolute;
    top: 150px;
    right: 250px;
    width: 400px;
    height: 320px;
  }
  #list{
    position: absolute;
    top: 500px;
    bottom: 100px;
    right: 250px;
    width: 400px; 
  }
  .basic{
    background-color:floralwhite;
    display: inline-block;
    height: 20px;
    line-height: 20px;
    text-align: center;
    font-size: 16px;
    margin: 1px 2px;
    border-radius: 15%;
    color:white;
    padding: 2px;
  }
  .n{
    background-color: blue;
  }
  .v{
    background-color: orange;
  }
  .a{
    background-color: red;
  }
  .d{
    background-color: green;
  }
  .nz{
    background-color: #5756D7;
  }
  .r{
    background-color: #1accf9;
  }
  .m{
    background-color: #F02595;
  }
  .q{
    background-color: #FBC285;
  }
  .c{
    background-color: #96F45E;
  }
  .u{
    background-color: #AD56D7;
  }
  .x{
    background-color: #919592;
  }
</style>